@import 'globals';

div.page {
  display: flex;
  justify-content: space-between;
  height: 100%;
}
section {
  width: 80%;
}
section > h1 {
  display: inline-block;
  width: 80%;
}
section > div:not(.about-menu, .whoops-404),
section > section {
  margin: 1em;
}

section.events,
section.products,
section.contact {
  margin: 1em;
  display: flex;
  justify-content: center;
  align-items: center;

  h1 {
    font-size: 3em;
    color: darkblue;
  }
}
div.main > h1 {
  font-size: 3em;
  color: darkslateblue;
  margin-bottom: 20px;
}

div.home {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  h1 {
    font-size: 3em;
    color: darkslateblue;
    margin-bottom: 20px;
  }

  > nav {
    background-color: darkslateblue;
    padding: 1em;
    border-top: dashed 0.1em ghostwhite;
    border-bottom: dashed 0.1em ghostwhite;
    width: calc(100% - 2em);
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;

    a {
      font-size: 2em;
      color: ghostwhite;
      flex-basis: 200px;
    }
  }
}

section.about {
  > section {
    padding: 2em;
  }
}

div.whoops-404 {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  display: flex;
  width: 100%;
  height: 100%;
  margin: 0;
  justify-content: center;
  align-items: center;
  background-color: darkred;
  color: ghostwhite;
  font-size: 1.5em;

  a {
    color: white;
    text-decoration: underline;
    margin: 1em;
    &:hover {
      color: yellow;
      text-decoration: none;
    }
  }
}
