$main-font: sans-serif;

html,
body,
#root,
#root > div {
  height: 100%;
}

body {
  font-family: $main-font;
  font-size: 100%;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
th,
header {
  font-family: $main-font;
  font-size: 100%;
  padding: 0;
  margin: 0;
}

a {
  display: inline-block;
  text-decoration: none;
  color: black;
  &:hover {
    text-decoration: underline;
  }
}
